const MainDiv = styled.div`
  width: 100%;
  height: 600px;
  position: relative;
  border: none;
  outline: none;
  padding: 10px;
  

  &:before {
    position: absolute;
    content: "";
    height: 0;
    width: 0;
    border: 3px solid transparent;
    bottom: 0;
    left: 0;
    /* border-radius : 5%; */
    box-sizing: border-box;
  }

  &:after {
    position: absolute;
    content: "";
    height: 0;
    width: 0;
    border: 3px solid transparent;
    top: 0;
    right: 0;
    /* border-radius : 5%; */
    box-sizing: border-box;
  }

  &:hover:before {
    height: 600px;
    width: 100%;
    border: 3px solid rgb(31, 229, 222);
    /* border-radius : 5%; */
    border-right: none;
    border-bottom: none;
    transition: height 0.5s linear, width 0.5s linear 0.5s;
  }

  &:hover:after {
    height: 600px;
    width: 100%;
    border: 3px solid rgb(31, 229, 222);
    /* border-radius : 5%; */
    border-left: none;
    border-top: none;
    transition: height 0.5s linear, width 0.5s linear 0.5s;
  }
`;
